import React, { Component } from 'react';

//Components
import Todo from './Todo';
import AddTodo from './AddTodo';
import ErrorMessage from './ErrorMessage';

class TodoFeed extends Component {
	state = {
		todos: [],
		error: null
	}

	componentDidMount(){
		this.fetchTodos();
	}

	handleSubmit = ({task}) => {
		const { todos } = this.state;
		todos.push({
			task,
			_id: Math.random(),
			done: false
		});
		this.setState({ todos });
	}

	handleDelete = id => {
		const { todos } = this.state;
		const newTodos = todos.filter(item => item._id !== id);
		this.setState({ todos: newTodos });
	}

	changeState = todo => {
		const { todos } = this.state;
		todos.map(item => {
			if(item._id === todo._id) item.done = !item.done;
			return item;
		});
		this.setState({ todos });
	}

	fetchTodos = () => {
		this.setState({todos: TODOS});
	}

	render() {
		const todos = this.state.todos.map(todo => 
			<Todo key={ todo._id } handleDelete={ this.handleDelete } changeState={ this.changeState } todo={ todo }/>
		);

		return (
			<div className="todo-feed">
				{ this.state.error ? <ErrorMessage error={this.state.error} /> : '' }
				<AddTodo handleSubmit={this.handleSubmit}/>
				{ todos.length > 0 ? todos : 'No todos to display' }
			</div>
		);
	}
}

export default TodoFeed;

const TODOS = [ 
	{ _id: Math.random(), done: false, task: 'Clean room'},
	{ _id: Math.random(), done: true, task: 'Do the dishes'},
	{ _id: Math.random(), done: true, task: 'Feed cat'},
	{ _id: Math.random(), done: false, task: 'Go for a walk'},
];